/**
* .
* @class Eroor
* @constructor
* @param {int} line - .
* @param {string} content - .
* @param {string} message - .
*/
function Error (line, content, message) {
  this.line = line;
  this.content = content;
  this.message = message;
  this.print = function () {
    let output = ":" + this.line + "\n:" + this.content + "\n:" + this.message;
    return output;
  };
}

function GrammarError (content, message) {
  this.content = content;
  this.message = message;
  this.print = function () {
    return {
      content: this.content, // '\n'
      message: this.message
    };
  };
}
export {
  Error,
  GrammarError
};
